.contacts {
  padding-bottom: 40px;
  @media screen and (max-width: 767.98px) {
    background-image: linear-gradient(
      to bottom,
      #ffffff 21%,
      #fff5f6 21%,
      #fff5f6 100%
    );
  }
  @media screen and (min-width: 768px) {
    background-image: linear-gradient(
      to bottom,
      #ffffff 44%,
      #fff5f6 44%,
      #fff5f6 100%
    );
  }
  @media screen and (min-width: 1200px) {
    background-image: linear-gradient(
      to bottom,
      #ffffff 13%,
      #fff5f6 13%,
      #fff5f6 100%
    );
    padding-bottom: 120px;
  }
}

.contacts__list {
    display: flex;
    @media screen and (max-width: 767.98px) {
        flex-direction: column; 
        margin-bottom: 32px;       
    }
    @media screen and (min-width:768px) {
    margin-left: -32px;
    margin-bottom: 60px;
  }
}

.contacts__item {
  border: 1px solid rgba(225, 225, 225, 1);
  border-radius: 24px;
  box-shadow: 0 8px 30px 0 rgba(212, 20, 67, 0.1);
  background-color: var(--bcg-color);
  @media screen and (max-width: 767.98px) {
    padding: 40px 32px;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
  @media screen and (min-width: 768px) {
    margin-left: 32px;
    width: calc((100% - 96px) / 3);
    padding: 40px 32px;
  }
  @media screen and (min-width: 1200px) {
    padding: 39px 40px 41px 40px;
  }
}

.contacts__category-repr {
  font-family: var(--standard-ff), sans-serif;
  font-weight: 500;
  line-height: 1.86;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--latte-color);
  border-radius: 4px;
  @media screen and (max-width: 767.98px) {
    font-size: 12px;
    margin: 0 auto 20px 0;
    width: 80px;
    height: 16px;
  }
  @media screen and (min-width: 768px) {
    font-size: 16px;
    margin: 0 auto 32px 0;
    width: 73px;
    height: 28px;
  }
  @media screen and (min-width: 1200px) {
    width: 75px;
  }
}

.contacts__category-repr--color {
  background-color: var(--soft-green-color);
  @media screen and (max-width: 767.98px) {
    width: 130px;
  }
  @media screen and (min-width: 768px) {
    width: 138px;
  }
}

.contacts__town {
  font-family: var(--standard-ff), sans-serif;
  font-weight: 500;
  line-height: 1.86;
  letter-spacing: 0.04em;
  color: var(--text-color);
  display: flex;
  align-items: center;
  background-color: transparent;
  @media screen and (max-width: 767.98px) {
    font-size: 12px;
    margin: 0 auto 8px 0;
    height: 22px;
  }
  @media screen and (min-width: 768px) {
    font-size: 14px;
    margin: 0 auto 12px 0;
    height: 26px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    margin: 0 auto 8px 0;
    height: 30px;
    line-height: 1.83;
  }
}

.contacts_town-subtitle {
  font-family: var(--standard-ff), sans-serif;
  font-weight: 500;
  color: var(--secondary-text-color);
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 767.98px) {
    font-size: 12px;
    line-height: 1.5;
    margin: 0 auto 24px 0;
    width: 179px;
    height: 36px;
  }
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.66;
    margin: 0 auto 32px 0;
    width: 159px;
    height: 65px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.83;
    width: 250px;
    height: 50px;
  }
}

.contacts__work-schedule {
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  @media screen and (max-width: 767.98px) {
    padding-top: 24px;
    padding-bottom: 24px;
    margin: 0 auto 24px 0;
  }

  @media screen and (min-width: 768px) {
    padding-top: 32px;
    padding-bottom: 32px;
    margin: 0 auto 32px 0;
  }
}

.contacts__working-days {
  @media screen and (max-width: 767.98px) {
    margin-bottom: 8px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 12px;
  }
}

.contacts__days-time {
  font-family: var(--standard-ff), sans-serif;
  font-weight: 500;
  background-color: transparent;
  display: flex;
  align-items: center;
  color: var(--text-color);
  margin: 0 auto 0 0;
  line-height: 1.86;
  height: 22px;
  @media screen and (max-width: 767.98px) {
    font-size: 12px;
    height: 22px;
  }
  @media screen and (min-width: 768px) {
    font-size: 14px;
    height: 26px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    height: 30px;
  }
}

.contacts__days-time--color {
  color: var(--subtitle-color);
  text-transform: uppercase;
}

.contacts__weekend {
  margin: 0 auto 0 0;
}
.contacts__communication {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto 0 0;
}

.contacts__tel-email {
  font-family: var(--standard-ff), sans-serif;
  font-weight: 500;
  color: var(--secondary-text-color);
  margin: 0 auto 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (max-width: 767.98px) {
    font-size: 12px;
    line-height: 1.5;
    height: 18px;   
}
@media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.83;
    height: 24px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    height: 25px;
  }
  &:hover,
  &:focus {
    color: var(--title-color);
  }
}

.contacts__modal-btn {
  display: flex;
  justify-content: center;
  @media screen and (max-width: 767.98px) {
    flex-direction: column;
  }
  @media screen and (min-width: 768px) {
    flex-direction: row;
  }
}

.contacts__btn-locations {
  height: 44px;
  border: 0;
  border-radius: 22px;
  background-color: #d41342;
  font-family: var(--standard-ff), sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  color: #ffffff;
  padding: 0;
  cursor: pointer;
  transition: background-color 0.25s ease-in-out, transform 0.25s ease-in-out;
  @media screen and (max-width: 767.98px) {
    width: 161px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
  }
  @media screen and (min-width: 768px) {
    width: 191px;
    margin-left: 0;
    margin-right: 14.5px;
  }
  @media screen and (min-width: 1200px) {
    width: 198px;
    margin-right: 12px;
  }
  &:hover,
  &:focus {
    background-color: rgba(212, 19, 66, 0.8);
    transform: scale(0.9);
  }
}

.contacts__btn-franchise {
  height: 44px;
  border: 0;
  border-radius: 22px;
  background-color: var(--bcg-color);
  font-family: var(--standard-ff), sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  color: var(--title-color);
  padding: 0;
  cursor: pointer;
  transition: background-color 0.25s ease-in-out, transform 0.25s ease-in-out;

  @media screen and (max-width: 767.98px) {
    width: 161px;
    margin-left: auto;
    margin-right: auto;
  }
  @media screen and (min-width: 768px) {
    width: 174px;
  }
  @media screen and (min-width: 1200px) {
    width: 168px;
  }
  &:hover,
  &:focus {
    background-color: rgba(165, 151, 155, 0.5);
    transform: scale(0.9);
  }
}

.contacts__more {
@media screen and (max-width: 767.98px) {
    margin-left: 8px;
  }

  @media screen and (min-width: 768px) {
    margin-left: 13px;
  }
}

.location__map {
  width: 260px;
  height: 300px;
  border: none;
  margin-bottom: 30px;

  @include desktop {
    width: 450px;
  }
}

.location__town {
  font-family: var(--standard-ff), sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  color: var(--text-color);

  @include desktop {
    font-size: 16px;
    line-height: 1.83;
  }
}

.location__subtitle {
  font-family: var(--standard-ff), sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: var(--secondary-text-color);

  @include desktop {
    font-size: 16px;
    line-height: 1.83;
  }
}
