.products {
  width: 100%;
  margin: 0 auto;
  padding: 60px 0;
  background-color: var(--bcg-color);
}

.products__container {
  @media screen and (min-width: 320px) {
    width: 320px;
    padding: 0 20px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 32px;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
    padding: 0 16px;
  }
}

.products__label {
  text-transform: uppercase;
  font-family: var(--title-ff);
  font-style: regular;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.16;
  text-align: center;
  vertical-align: top;
  color: var(--subtitle-color);
  margin-bottom: 15px;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
    margin-bottom: 18px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 16px;
  }
}

.products__title {
  font-family: var(--title-ff);
  font-style: regular;
  text-transform: uppercase;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.14;
  text-align: center;
  vertical-align: top;
  color: var(--title-color);
  margin-bottom: 140px;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.14;
    margin-bottom: 138px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.13;
    margin-bottom: 281px;
  }
}

.products__list {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-wrap: wrap;
  display: flex;

  @media screen and (min-width: 768px) {
    flex-wrap: nowrap;
    gap: 16px;
    display: flex;
  }

  @media screen and (min-width: 1200px) {
    flex-wrap: nowrap;
    gap: 32px;
    display: flex;
  }
}

.products__item {
  width: 100%;
}

.products__item:not(:last-child) {
  @media screen and (max-width: 767px) {
    margin-bottom: 130px;
  }
}

.products__card-text {
  border-radius: 24px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 160px 53px 32px;
  display: flex;
  position: relative;
  @media screen and (min-width: 768px) {
    padding: 118px 25px 32px;
  }
  @media screen and (min-width: 1200px) {
    padding: 165px 81px 32px 78px;
  }
}

.products__card-text-pink::before {
  background-image: url(../images/products/products-icecream-mobile.png);
  @media screen and (max-width: 767px) {
    background-image: url(../images/products/products-icecream-mobile.png);
  }
  @media screen and (min-resolution: 2dppx) and (max-width: 767px) {
    background-image: url(../images/products/products-icecream-mobile@2x.png);
  }
  @media screen and (min-width: 768px) {
    background-image: url(../images/products/products-icecream-tablet.png);
  }
  @media screen and (min-width: 768px) and (min-resolution: 2dppx) {
    background-image: url(../images/products/products-icecream-tablet@2x.png);
  }

  @media screen and (min-width: 1200px) {
    background-image: url(../images/products/products-icecream-desktop.png);
  }

  @media screen and (min-width: 1200px) and (min-resolution: 2dppx) {
    background-image: url(../images/products/products-icecream-desktop@2x.png);
  }
}

.products__card-text-latte::before {
  background-image: url(../images/products/products-icecoffee-mobile.png);
  @media screen and (max-width: 767px) {
    background-image: url(../images/products/products-icecoffee-mobile.png);
  }

  @media screen and (min-resolution: 2dppx) and (max-width: 767px) {
    background-image: url(../images/products/products-icecoffee-mobile@2x.png);
  }

  @media screen and (min-width: 768px) {
    background-image: url(../images/products/products-icecoffee-tablet.png);
  }

  @media screen and (min-width: 768px) and (min-resolution: 2dppx) {
    background-image: url(../images/products/products-icecoffee-tablet@2x.png);
  }

  @media screen and (min-width: 1200px) {
    background-image: url(../images/products/products-icecoffee-desktop.png);
  }

  @media screen and (min-width: 1200px) and (min-resolution: 2dppx) {
    background-image: url(../images/products/products-icecoffee-desktop@2x.png);
  }
}

.products__card-text-green::before {
  background-image: url(../images/products/products-milkshakes-mobile.png);
  @media screen and (max-width: 767px) {
    background-image: url(../images/products/products-milkshakes-mobile.png);
  }

  @media screen and (min-resolution: 2dppx) and (max-width: 767px) {
    background-image: url(../images/products/products-milkshakes-mobile@2x.png);
  }

  @media screen and (min-width: 768px) {
    background-image: url(../images/products/products-milkshakes-tablet.png);
  }

  @media screen and (min-width: 768px) and (min-resolution: 2dppx) {
    background-image: url(../images/products/products-milkshakes-tablet@2x.png);
  }

  @media screen and (min-width: 1200px) {
    background-image: url(../images/products/products-milkshakes-desktop.png);
  }

  @media screen and (min-width: 1200px) and (min-resolution: 2dppx) {
    background-image: url(../images/products/products-milkshakes-desktop@2x.png);
  }
}

.products__card-text::before {
  content: '';
  width: 280px;
  height: 280px;
  z-index: 2;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  transform: translateY(-46%);
  @media screen and (min-width: 768px) {
    width: 224px;
    height: 224px;
    transform: translateY(-55%);
  }
  @media screen and (min-width: 1200px) {
    width: 368px;
    height: 394px;
    transform: translateY(-65%);
  }
}

.products__card-text-pink {
  background-color: var(--pink-hero-color);
}

.products__card-text-latte {
  background-color: var(--latte-color);
}

.products__card-text-green {
  background-color: var(--soft-green-color);
}

.products__name {
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  font-family: var(--title-ff);
  font-style: regular;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.15;
  align-items: center;
  vertical-align: top;
  color: var(--bcg-color);

  @media screen and (min-width: 768px) {
    font-size: 20px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.products__name::after {
  content: '';
  width: 26px;
  height: 6px;
  background-image: url(../images/rewievs/3dots26-6.svg);
  margin: 30px auto 0;
  display: block;
}

.products__text {
  font-family: var(--standard-ff);
  text-align: center;
  letter-spacing: 0.04em;
  font-style: bold;
  font-size: 14px;
  line-height: 1.5;
  font-style: normal;
  font-weight: 700;
  align-items: center;
  vertical-align: top;
  color: var(--bcg-color);
  margin-top: 32px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.78;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.75;
  }
}

.products__link {
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: var(--bcg-color);
  border: 0;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
  display: flex;
  transition: background-color 0.25s ease-in-out;

  &:hover {
    background-color: var(--btn-hover-color);
  }
}

.products__icon {
  width: 40px;
  height: 40px;
}
