.section-reviews {
  padding-bottom: 60px;
  @include tablet {
    padding-bottom: 80px;
  }

  @include desktop {
    padding-bottom: 120px;
  }
}

.swiper {
  width: 280px;
  height: 100%;

  @include tablet {
    width: 736px;
  }
  @include desktop {
    width: 968px;
  }
}

.swiper-slide {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}

.swiper-horizontal > .swiper-pagination-bullets {
  position: absolute;
  bottom: 0;
}

.swiper .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  display: inline-block;
  border-radius: 50%;
  background: #d9d9d9;
  opacity: 1;
}

.swiper .swiper-pagination-bullet-active,
.swiper .swiper-pagination-bullet-active-main {
  background-image: url(/src/images/pag-house.png);
  background-position: 50% 50%;
  background-size: 100%;
  background-color: transparent;
  border-radius: 0;
}

.client {
  margin-bottom: 32px;
  @include tablet {
    margin-bottom: 20px;
  }

  @include desktop {
    margin-bottom: 16px;
  }
}

.swiper__text-box {
  width: 100%;
  padding: 43px 50px 17px 50px;
  background-color: #fafafa;
  margin-bottom: 36px;
  position: relative;
  @include tablet {
    width: 620px;
    padding: 37px 6px 36px 6px;
    margin-bottom: 24px;
  }
  @include desktop {
    width: 968px;
    padding: 46px 74px 31px 72px;
    margin-bottom: 36px;
  }
  &::before {
    content: '';
    background-image: url(../images/rewievs/coma.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 54px;
    height: 40px;
    left: 53;
    top: 30;
    position: absolute;
    z-index: 1;
    @include tablet {
      width: 60px;
      height: 40px;
      left: 6;
      top: 20;
    }
    @include desktop {
      width: 54px;
      height: 40px;
      left: 71;
      top: 31;
    }
  }
  &::after {
    content: '';
    background-image: url(../images/rewievs/3dots32-4.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 32px;
    height: 4px;
    left: calc(50% - 16px);
    bottom: -16px;
    position: absolute;
    @include tablet {
      bottom: -4px;
    }
    @include desktop {
      bottom: -16px;
    }
  }
}

.swiper__text {
  width: 100%;
  padding: 0 8px 0 15px;
  background-image: repeating-linear-gradient(
    to top,
    #e1e1e1,
    #e1e1e1 1px,
    #fafafa 1px,
    #fafafa 31px
  );
  @include tablet {
    padding: 0 50px 0 45px;
  }
  @include desktop {
    padding: 0 36px 0 35px;
  }
}

.slider-text {
  font-family: var(--standard-ff);
  font-weight: 500;
  color: var(--text-color);
  font-size: 12px;
  line-height: 2.55;
  text-align: center;
  position: relative;
  z-index: 3;
  @include tablet {
    font-size: 14px;
    line-height: 2.16;
    letter-spacing: 0.04em;
  }
  @include desktop {
    font-size: 16px;
    line-height: 1.86;
  }
}

.slider-name {
  font-family: var(--standard-ff);
  font-weight: 500;
  color: var(--text-color);
  font-size: 14px;
  line-height: 1.86;
  text-align: center;
  margin-bottom: 16px;
  @include tablet {
    font-size: 16px;
    margin-bottom: 32px;
  }
  @include desktop {
    font-size: 20px;
    margin-bottom: 32px;
  }
}
